body {
    font-family: "Century Gothic", 'Lato', sans-serif;
}
html{
    scroll-behavior: smooth; 
    font-size : 14px;
}
a {
text-decoration: none;
}
@keyframes blink {
    from,to {
        border-color: transparent
    }
    
    50%{
        border-color: white;
    }
}

  @keyframes fadein {
    from {
        opacity:0;
    }
    to{
        opacity:1;
    }
}
@-moz-keyframes fadein { /* Firefox */
    from {
        opacity:0;
    }
    to {
        opacity:1;
    }
}
@-webkit-keyframes fadein { /* Safari and Chrome */
    from {
        opacity:0;
    }
    to {
        opacity:1;
    }
}
@-o-keyframes fadein { /* Opera */
    from {
        opacity:0;
    }
    to {
        opacity: 1;
    }
}
.et-hero-tabs{
    background: url(../img/main.png) !important;
    background-size : 100% 100% !important;
    background-repeat: no-repeat !important;
    -webkit-animation: fadein 1.5s;
    

}
#mypic{
    width: 100px;
    height: 100px;
    top : -10%;
    position: relative;
}
.et-slide{
    background-color: white;
    width: 100%;
    padding-top: 3%;
    padding-bottom : 3%;
}
.et-hero-tabs{
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
height: 100vh;
position: relative;
background: #eee;
    text-align: center;
    padding: 0 2em;

}

.et-hero-tabs h1 {
    border-right: 0.1em solid white;
    animation: blink 1s step-end infinite;
    font-size: 3.6vw;
    white-space: pre;
    line-height: 1;
    letter-spacing: 0.1rem;
    color: white;
    position: relative;
    top : -10%;
    margin: 0;
}

.et-hero-tabs-container {
display: flex;
flex-direction: row;
bottom: 0;
position: fixed;
width: 100%;
height: 55px;
box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
background: #fff;
z-index: 10;

}
.et-hero-tabs-container--top {
    position: fixed;
    top : 0;
}
.et-hero-tab {
display: flex;
justify-content: center;
align-items: center;
flex: 1;
color: #000;
letter-spacing: 0.1rem;
    transition: all 0.5s ease;
    font-size: 0.8rem;
  
}
.et-hero-tab:hover {
    color:white;
background: darkgray;
    transition: all 0.5s ease;
}

.et-hero-tab-slider {
position: absolute;
bottom: 0;
width: 0;
height: 6px;
background: #66B1F1;
transition: left 0.3s ease;
}

